import React, { useState, useEffect } from 'react';
import { Box, TextField, Button, Paper, Typography, IconButton, Divider } from '@mui/material';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import RefreshIcon from '@mui/icons-material/Refresh';
import CircularProgress from '@mui/material/CircularProgress'; // Add this import for CircularProgress
import SectionToggle from './SectionToggle';

// import SplitBasicExample from './UnitSelect';

import SplitBasicExample from './SplitBasicExample';

const LoadingOverlay = () => (
  <Box sx={{
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 10
  }}>
    <CircularProgress /> {/* Material-UI loading spinner */}
  </Box>
);

// Day mode color variables
const dayModeColors = {
  threadTitle: '#000000',
  background: '#ffffff',
  userText: 'black',
  aiText: 'red',
  button: {
    text: 'darkgrey',
    border: 'grey',
    hoverBorder: 'darkgrey',
    hoverBackground: '#f5f5f5',
  },
  runButton: {
    background: '#0757b3',
    hover: '#388e3c',
  },
  refreshButton: {
    background: 'darkgrey',
    hover: 'grey',
  },
  inputField: {
    border: 'darkgrey',
  },
};

// Night mode color variables
const nightModeColors = {
  threadTitle: '#ffffff',
  background: '#828282',
  userText: '#ffffff',
  aiText: '#40ad83',
  button: {
    text: '#ffffff',
    border: '#ffffff',
    hoverBorder: 'lightgrey',
    hoverBackground: '#ffffff',
  },
  runButton: {
    background: '#006400',
    hover: '#004d00',
  },
  refreshButton: {
    background: 'white',
    hover: 'white',
  },
  inputField: {
    border: '#ffffff',
  },
};

const InteractiveGPT = ({ sessionKey } ) => {
  // console.log("Test");
  const [activeMenu, setActiveMenu] = useState('thread'); // 'thread' or 'history'

  const [inputText, setInputText] = useState('');
  const [botResponses, setBotResponses] = useState([]);
  const [threadId, setThreadId] = useState(null);
  const [isNightMode, setIsNightMode] = useState(false);
  const [message, setMessage] = useState('');
  const [showMessage, setShowMessage] = useState(false);
  const [isLoading, setIsLoading] = useState(false);  
  const [modelType, setModelType] = useState('(Select)'); // Declare modelType state before using it in useEffect
  const [temporaryMessage, setTemporaryMessage] = useState(''); // State for temporary message
  // const [sessionKey, setSessionKey] = useState('');


  useEffect(() => {
    console.log("Model Type is:", modelType)
    if (modelType) {
      console.log("Model Type changed to:", modelType);
      setTemporaryMessage(`You have selected the ${modelType} option.`);
      // Optionally, clear the message after a delay
      const timer = setTimeout(() => {
        setTemporaryMessage('');
      }, 3000); // Clear message after 3 seconds
      return () => clearTimeout(timer); // Clear timeout if the component unmounts
    }
  }, [modelType]);

  // const [modelType, setModelType] = useState('');  // Add this line to define modelType state
 

  const fetchAPI = async (url, method, body) => {
    const response = await fetch(url, {
      method: method,
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${sessionKey}`, // Use sessionKey from props
      },
      body: JSON.stringify(body),
    });
    return response.json();
  };

  // Use fetchAPI as needed within InteractiveGPT


  

  const handleExampleOption = (exampleQuery) => {
    setInputText(exampleQuery);
  };
  // Function to create a new thread
  const createThread = async () => {
    setIsLoading(true);
  try {
    const response = await fetchAPI('https://harvard-physics-hosting.uw.r.appspot.com/create_thread', 'POST', {});
    setThreadId(response.thread_id);
  } finally {
    setIsLoading(false);
  }
  };

  // Function to send user input and retrieve response
  const sendAndRetrieve = async (userInput, modelType) => {
    setIsLoading(true);
    let url = 'https://harvard-physics-hosting.uw.r.appspot.com/send_and_retrieve_with_logging'; // Default URL

    // Change URL based on modelType
    if (modelType === 'ProblemBank') {
      console.log("PROBLEM")
      url = 'https://harvard-physics-hosting.uw.r.appspot.com/send_and_retrieve_problem'; // Replace with the actual URL for the other model
    }

    try {
      const response = await fetchAPI(url, 'POST', {
        thread_id: threadId,
        message_content: userInput,
      });
      return response;
    } finally {
      setIsLoading(false);
    }
  };

  

  // Initialize thread on component mount
  useEffect(() => {
    createThread();
  }, []);

  const handleInputChange = (event) => {
    setInputText(event.target.value);
  };


  const handleRun = async () => {
    setTemporaryMessage(''); // Clear the temporary message when running the model

    if (!inputText.trim()) {
      
      setMessage("Please enter some text before running.");
      setShowMessage(true);
      setTimeout(() => setShowMessage(false), 3000); // Message will disappear after 3 seconds
      return;
    }
  
    setIsLoading(true);
    try {
      const response = await sendAndRetrieve(inputText, modelType);
      setBotResponses([...botResponses, { user: inputText, bot: response }]);
      setInputText('');
    } catch (error) {
      console.error("An error occurred while fetching data:", error);
    } finally {
      setIsLoading(false);
    }
  };
  

  const handleRefresh = () => {
    setInputText('');
    setBotResponses([]);
    createThread(); // Create a new thread on refresh
  };


  const buttonHeight = '34px';
  const exampleOptions = [
    { label: "What is __ topic about?", query: "What is" },
    { label: "Summarize lectures..", query: "Summarize lectures.." },
    { label: "I'm confused on..", query: "I'm confused on this topic" },
  ];

  const colors = isNightMode ? nightModeColors : dayModeColors;

  const handleToggleMode = () => {
    setIsNightMode(!isNightMode);
  };

  return (
    
    <Paper elevation={3} sx={{ display: 'flex', flexDirection: 'column', height: '597px', borderRadius: '16px', padding: '1rem', backgroundColor: colors.background }}>
      {isLoading && <LoadingOverlay />}
      {showMessage && (
        <Box sx={{ position: 'absolute', top: 0, width: '50%', backgroundColor: '#f3f3f3', textAlign: 'center', padding: '10px' , borderRadius: '16px'}}>
          <Typography>{message}</Typography>
          
        </Box>
      )}
        {temporaryMessage && (
        <Box sx={{ textAlign: 'center', my: 2 }}>
          <Typography sx={{ color: 'green' }}>
            {temporaryMessage}
          </Typography>
        </Box>
      )}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography variant="h6" component="div" sx={{ fontWeight: 'bold', mb: 2, fontSize: '1rem', textAlign: 'center', color: colors.threadTitle }}> 
          Thread
        </Typography>
        {/* <Switch checked={isNightMode} onChange={handleToggleMode} /> */}
      </Box>
      <Divider sx={{ mb: 2, borderColor: 'black'}} />

      <Box sx={{ flexGrow: 1, overflow: 'auto' }}>
        {botResponses.length === 0 ? (
          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', height: '100%'}}>
            <Typography sx={{ fontWeight: '1.4rem'}} >
              How can I help?
            </Typography>
          </Box>
        ) : (
          botResponses.map((response, index) => (
            <Box key={index} sx={{ mb: 1 }}>
              <Typography sx={{ fontWeight: 'bold', color: colors.userText, mb: 0.5, pl: 0, fontSize: '0.85rem' }}> 
                User:
              </Typography>
              <Typography sx={{
                border: `1px solid ${colors.button.border}`,
                padding: '6px',
                backgroundColor: colors.background,
                borderRadius: '15px',
                mb: 1,
                pl: 2,
                fontSize: '0.85rem'
              }}>
                {response.user}
              </Typography>
              <Typography sx={{ fontWeight: 'bold', color: colors.aiText, mb: 0.5, pl: 0, fontSize: '0.85rem' }}> 
                AI:
              </Typography>
              <Typography sx={{
                border: `1px solid ${colors.button.border}`,
                backgroundColor: colors.background,
                padding: '6px',
                borderRadius: '15px',
                pl: 2,
                fontSize: '0.85rem'
              }}>
                {response.bot}
              </Typography>
            </Box>
          ))
        )}
      </Box>

      {inputText === '' && botResponses.length === 0 && (
      <Box sx={{ display: 'flex', justifyContent: 'center', flexWrap: 'wrap', gap: 2, mt: 2 }}>
        {exampleOptions.map((option, index) => (
          <Button
            key={index}
            variant="outlined"
            onClick={() => handleExampleOption(option.query)}
            sx={{
              border: `1px solid ${colors.button.border}`,
              color: colors.button.text,
              borderRadius: '15px',
              textTransform: 'lowercase',
              '&:hover': {
                border: `1px solid ${colors.button.hoverBorder}`,
                backgroundColor: colors.button.hoverBackground
              }
            }}
          >
            {option.label}
          </Button>
        ))}
      </Box>
      )}
  
      <Box sx={{ display: 'flex', alignItems: 'center', mt: 2 }}>
  
        <TextField
          fullWidth
          variant="outlined"
          placeholder="Enter your query. Be as descriptive as possible."
          value={inputText}
          onChange={handleInputChange}
          
          margin="normal"
          sx={{
            '& .MuiOutlinedInput-root': {
              borderRadius: '16px',
              '&.Mui-focused fieldset': {
                borderColor: colors.inputField.border,
              },
            },
            '& input': {
              fontSize: '0.8rem',
            },
          }}
        />
  
  <SplitBasicExample
  modelType={modelType}
  // marginTop= '10px'
  onModelTypeChange={setModelType}
  onRunClick={handleRun} // Pass the handleRun function as a prop
/>


        <IconButton
          color="default"
          onClick={handleRefresh}
          sx={{
            ml: 0,
            borderRadius: '8px 8px 8px 8px',
            marginTop: '9px',
            marginLeft: '5px',
            bgcolor: colors.refreshButton.background,
            height: '39px',
            // boxShadow: '2px 2px 3px rgba(0, 0, 0, 0.5)',
            // transform: 'translateY(1px)',
            '&:hover': {
              bgcolor: colors.refreshButton.hover,
              // boxShadow: '2px 2px 5px rgba(0, 0, 0, 0.7)',
              // transform: 'translateY(2px)',
            },
          }}
        >
          <RefreshIcon />
        </IconButton>
      </Box>
      {/* <Divider sx={{ mb: 2, borderColor: 'black'}} /> */}
      {/* <SectionToggle onSectionChange={(section) => console.log('Section changed to:', section)} /> */}

    </Paper>
  );
};

export default InteractiveGPT;
