import React from 'react';
import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Dropdown from 'react-bootstrap/Dropdown';
import 'bootstrap/dist/css/bootstrap.min.css';

function SplitBasicExample({ modelType, onModelTypeChange, onRunClick }) {
    console.log('HI');
    const buttonStyle = {
        width: '100%',
        fontSize: '16px',
    };

    const handleSelect = (eventKey) => {
        console.log(`Model selected: ${eventKey}`);
        onModelTypeChange(eventKey);
    };
    
    const toggleStyle = {
        ...buttonStyle,
        width: 'auto',
        marginLeft: '1px',
        marginTop: '9px', // Add margin-top here
    };

    const leftButtonStyle = {
        ...buttonStyle,
        marginTop: '9px',
        marginLeft: '10px', // Add left margin here
    };

    const arrowStyle = {
        width: '20px',
        height: '20px',
        marginLeft: '0px',
    };

    return (
        <Dropdown as={ButtonGroup} style={{ width: '30%' }} onSelect={handleSelect}>
            <Button variant="success" style={leftButtonStyle} onClick={onRunClick}>
                {`Run Model: ${modelType || 'Select Model'}`}
            </Button>

            <Dropdown.Toggle split variant="success" id="dropdown-split-basic" style={toggleStyle}>
                <span style={arrowStyle}></span>
            </Dropdown.Toggle>

            <Dropdown.Menu style={{ width: '100%' }}>
                <Dropdown.Item eventKey="Content">Content</Dropdown.Item>
                <Dropdown.Item eventKey="Lectures">Lectures</Dropdown.Item>
                <Dropdown.Item eventKey="ProblemBank">Problem Bank</Dropdown.Item>
                <Dropdown.Item eventKey="Images">Images</Dropdown.Item>
                <Dropdown.Item eventKey="Unlimited GPT3.5">GPT 3.5</Dropdown.Item>
            </Dropdown.Menu>
        </Dropdown>
    );
}

export default SplitBasicExample;
