import React, { useState } from 'react';
import { Paper, Typography, Box } from '@mui/material';
import Dropdown from 'react-bootstrap/Dropdown';
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import 'bootstrap/dist/css/bootstrap.min.css';

const BasicButtonExample = () => {
  const [showDropdown, setShowDropdown] = useState(false);

  const toggleDropdown = () => setShowDropdown(!showDropdown);

  return (
    <Paper
      elevation={3}
      sx={{
        height: '70px',
        borderRadius: '16px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        padding: '0 16px',
        boxSizing: 'border-box',
        fontFamily: 'Roboto, sans-serif',
        transition: 'background-color 0.3s',
        marginY: '15px', // Add 15px margin on top and bottom
        cursor: 'pointer', // Change cursor to pointer
        position: 'relative', // Needed for absolute positioning of the icons
        '&:hover': {
          backgroundColor: '#c1c1c1',
        },
      }}
      onClick={toggleDropdown} // Make the entire Paper a toggle for the dropdown
    >
      <Box sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
        <Typography variant="subtitle1" component="div" sx={{ fontWeight: 'bold', color: '#000' }}>
          Unit 1: Electrostatics
        </Typography>
        <Typography variant="caption" display="block" sx={{ fontSize: '0.8rem', color: '#000' }}>
          Topic
        </Typography>
      </Box>
      {/* {showDropdown ? <ArrowDropUpIcon /> : <ArrowDropDownIcon />} */}
      <Dropdown show={showDropdown} onToggle={toggleDropdown}>
        <Dropdown.Toggle
          variant="transparent"
          id="dropdown-basic"
         
        />
        <Dropdown.Menu align="center">
          <Dropdown.Item eventKey="1">Unit 1</Dropdown.Item>
          <Dropdown.Item eventKey="2">Unit 2</Dropdown.Item>
          <Dropdown.Item eventKey="3">Unit 3</Dropdown.Item>
          {/* Add more Dropdown.Items for additional units */}
        </Dropdown.Menu>
      </Dropdown>
    </Paper>
  );
};

export default BasicButtonExample;
