import React from 'react';
import { Typography, Button, Box } from '@mui/material';
import { styled, keyframes } from '@mui/system';

// Define the keyframe animation
const slideUpAnimation = keyframes`
  from {
    bottom: -100%;
  }
  to {
    bottom: 50%;
    transform: translate(-50%, 50%);
  }
`;

// Create a styled Box component that uses the animation
const AnimatedBox = styled(Box)({
  position: 'fixed',
  left: '50%',
  transform: 'translateX(-50%)',
  width: '80%', // Adjust size as needed
  height: '60%', // Adjust size as needed
  backgroundColor: 'white',
  borderRadius: '16px',
  boxShadow: '0px 5px 20px rgba(0, 0, 0, 0.3)',
  zIndex: 10,
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  justifyContent: 'center',
  p: 3,
  animation: `${slideUpAnimation} 0.5s ease-out forwards`
});

// YourPopupComponent using AnimatedBox
const YourPopupComponent = ({ content, onClose, animate }) => {
  return (
    <AnimatedBox
      sx={{
        bottom: animate ? '50%' : '-100%', // Adjust initial and final position
      }}
    >
      <Typography variant="h6" sx={{ mb: 2 }}>{content}</Typography>
      {/* Add your buttons or content here */}
      <Button onClick={() => onClose()}>Close</Button>
    </AnimatedBox>
  );
};

export default YourPopupComponent;
